export default new Router({
    base: process.env.BASE_URL,
    routes: [{
            path: '/',
            redirect: '/recommend'
        },
        {
            path: '/recommend',
            name: 'recommend',
            component: NetRecommend,
            children: [{
                path: ':id',
                component: NetDisc
            }]
        },
        {
            path: '/singer',
            name: 'singer',
            component: NetSinger,
            children: [{
                path: ":id",
                component: SingerDetail
            }]
        },
        {
            path: '/mine',
            name: 'mine',
            component: NetMine
        },
        {
            path: '/videos',
            name: 'videos',
            component: NetVideo,
            children: [{
                path: ':id',
                component: VideoDetail
            }]
        },
        {
            path: '/list',
            name: 'list',
            component: NetList,
            children: [{
                path: ':id',
                component: NetDisc
            }]
        },
        {
            path: '/rank',
            name: 'rank',
            component: NetRank,
            children: [{
                path: ":id",
                component: TopList
            }]
        },
        {
            path: '/search',
            name: 'search',
            component: NetSearch,
            children: [{
                path: ":id",
                component: SingerDetail
            }]
        },
        {
            path: '/project',
            name: 'project',
            component: NetProject
        }
    ]
})